.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
  height: 40vmin;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: 20s linear infinite App-logo-spin;
    animation: 20s linear infinite App-logo-spin;
  }
}

.App-header {
  color: #fff;
  background-color: #282c34;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  display: flex;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.Login {
  text-align: center;
  background-color: #fcfcfc;
  width: 100%;
  height: 100vh;
}

.login-container {
  justify-content: center;
  align-items: center;
  height: 70vh;
  display: flex;
}

.login-form {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.login-form input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 250px;
  margin: 10px;
  padding: 10px;
}

.login-form button {
  color: #fff;
  cursor: pointer;
  background-color: #bffa60;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.login-form button:hover {
  background-color: #c3ea86;
}

.navbar {
  color: #fff;
  background-color: #bffa60;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.navbar-heading {
  margin: 0;
}

.navbar-flag {
  width: 30px;
  height: auto;
}

.App {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.candidates-row {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.candidates-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  display: flex;
}

.candidate-card {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 20px;
}

.candidate-image {
  object-fit: cover;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin-bottom: 10px;
}

.candidate-card input {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

.party-icon {
  object-fit: contain;
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

.delete-icon {
  color: red;
  cursor: pointer;
  font-size: 18px;
}

.delete-icon:hover {
  color: #8b0000;
}

.candidate-form {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 20px;
}

.candidate-form h2 {
  margin-bottom: 10px;
}

.save-button-container {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-left: 10px;
  display: flex;
}

.save-button {
  color: #fff;
  cursor: pointer;
  background-color: #bffa60;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .3s;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.save-button:hover {
  background-color: #c3ea86;
}

form {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

input {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

input[type="file"] {
  padding: 1px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #bffa60;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 18px;
}

button[type="submit"]:hover {
  background-color: #c3ea86;
}

h2 {
  margin-bottom: 20px;
