.poll-table-container {
  width: 100vw;
  margin-top: 50px;
}

.poll-table {
  border-collapse: collapse;
  width: 100%;
}

.poll-table th, .poll-table td {
  text-align: center;
  border: 1px solid #ccc;
  padding: 10px;
}

.poll-table th {
  background-color: #f0f0f0;
  font-weight: bold;
}

.poll-table td {
  background-color: #f9f9f9;
}

.poll-table tr:nth-child(odd) td {
  background-color: #fff;
}

.poll-table td button {
  color: #fff;
  cursor: pointer;
  background-color: #bffa60;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 14px;
  transition: background-color .2s;
}

.poll-table td button:hover {
  background-color: #c3ea86;
}

.App {
  font-family: Arial, sans-serif;
}

.candidates-row {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.candidates-grid {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  max-width: 1200px;
  padding: 20px;
  display: grid;
}

.candidate-card {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.candidate-image {
  object-fit: cover;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin-bottom: 10px;
}

.party-icon {
  width: 30px;
  height: 30px;
  margin: 10px auto;
}

.vote-button-container {
  margin-top: 20px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #bffa60;
  border: none;
  border-radius: 4px;
  padding: 8px 20px;
  font-size: 16px;
  transition: background-color .2s;
}

button:hover {
  background-color: #c3ea86;
}

@media screen and (max-width: 768px) {
  .candidates-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

.add-election-form {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  max-width: 400px;
  margin: 100px auto 0;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.add-election-form img {
  width: 300px;
  margin-left: -180px;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

.add-election-form h2 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 24px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  margin-bottom: 5px;
  font-weight: bold;
  display: block;
}

.form-group input {
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  width: 95%;
  padding: 8px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #bffa60;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .2s;
}

button:hover {
  background-color: #c3ea86;
}

